h1{background-color: white;
}
.container1 {
  
    background-color: aquas;
    margin-top: 70px;
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* 4 columnas */
    grid-auto-rows: 280px;
    grid-auto-columns: -520px;
    margin-left: 200px;

}
.container1 img{
width: 180px; /* Ancho del 100% para llenar las columnas */
height: auto; /* Altura automática para mantener la proporción */
margin-left: -47px;
margin-right: 20px;
margin-top: 12px;
}
.sello{
    width: 900px;
    height: 350px;
    background-color:rgba(255, 254, 255, 0.62);
    margin-left: 170px;
}
.logos{
    width: 22%;
    margin-top: 40px;
    float: left;
    margin-left: 50px;
}
.text2 {
    padding-top: 140px;
    padding-left: 250px;
    font-size: 20px;
}
@media (max-width: 768px) {
    .container1 {
      grid-template-columns: repeat(1, 1fr); /* 2 columnas en pantallas más pequeñas */
      margin-left: 12px;          
      margin-top: -1px;
      grid-row-gap:90px; 
    }
    .container1 img{
        width: 70%;
        margin-left: 62px;
    }
    .sello{
        width: 90%;
        height: 650px;
        margin-left: 21px;
        margin-top: 120px;
    }
    .text2{
        padding-top: 400px;
        position: absolute;
        padding-left: 32px;
        font-size: 18px;
    }
    .logos{
        margin-left: 32px;
    }
}