body{
    height: 100vh;
}
h1{
    font-family: Swis721 BT;
}
.container{
    width: 60%;
    height: 400px;
    background-color:  rgba(255, 254, 255, 0.62);
    margin-left: auto;
    margin-right: auto;
    margin-top: 60px;
}
.texto{
    padding-top: 22px;
    padding-left: 83px;
    text-align: center;
    font-family: Swis721 BT;
    font-size: 18px;
    font-weight: bold;
}
.logo{
    width: 200px;
    margin-right: 270px;
    margin-top: 30px;
}
.logo{
    margin-top: 5px;
}
@media(max-width:800px){
.texto{
    padding-left: 32px;
    text-align: left;
    
}
.logo{
    margin-left: 20px;
    margin-right: 40px;
}
.container{
    margin-top: 12px;
    width: 80%;
    height: 500px;
}
}