.box{
    width: 87%;
    height: 300px;
    margin-left: 100px;
    margin-top: 60px;
    background-color:rgba(255, 254, 255, 0.62);
    border-radius: 10px;
}
p{
    width: 70%;
    text-align: left;
    padding-left: 40px;
    padding-top: 77px;
    position: relative;
}
img{
    width: 20%;
    height: 270px;
    float: right;
    margin-right: 10px;
    margin-top: -183px;
    border-radius: 10px;
}
@media (max-width:890px){
.box{
    width: 80%;
    height: 820px;
    margin-left: 37px;
    margin-top: 40px;
}
img{
    width: 80%;
    height: auto;  
    margin-right:30px;
    margin-top: 30px;

}
p{
  padding-top: 42px;
}
}