h1{ 
    margin-left: 250px;
    width: 60%;
    border-radius: 20px;
    background-color: white;
    text-align: center;
}
.text{
    width: 70%;
    background-color: rgba(255, 254, 255, 0.62);
    padding: 12px 12px;
    margin-left: 180px;
}
.grid-container {
    margin-top: 230px;
    display: grid;
    grid-template-columns: repeat(4, 1fr); /* 4 columnas */
    grid-auto-rows: 280px;
    grid-auto-columns: -520px;
    
    grid-row-gap:10px; /* Altura automática para las filas */
    gap: 30px; /* Espacio entre las imágenes */
  }
  
  .grid-container img {
    width: 180px; /* Ancho del 100% para llenar las columnas */
    height: auto; /* Altura automática para mantener la proporción */
    margin-left: 47px;
    margin-right: autp;
  }
  

@media (max-width:800px) {
          
      h1{
        margin-left: 87px;
        font-size: 20px;
    }
    .text{
        margin-left: 29px;
        width: 80%;
        text-align: left;
    }
    @media (max-width: 768px) {
        .grid-container {
          grid-template-columns: repeat(2, 1fr); /* 2 columnas en pantallas más pequeñas */
          margin-left: 12px;          
          margin-top: -20px;
          grid-row-gap:120px; 
        }
      }
      
      /* Media query para pantallas aún más pequeñas */
      @media (max-width: 480px) {
        .grid-container {
          grid-template-columns: repeat(1, 1fr); /* 1 columna en pantallas muy pequeñas */
        }
        .grid-container img{
            width: 270px;
        }
      }
}