body{
  height: 100vh;
  font-family: Swis721 BT;
}
.Nav {
  background-color: #000000;
  padding: 10px 20px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

li {
  display: inline;
  margin-right: 20px;
}
.Link1{
  color: orange;
  font-weight: bold;
  text-decoration: none;
  font-size: 20px;
}

.Link {
  color: #fff;
  text-decoration: none;
}

.menu-icon {
  display: none; /* Oculta el icono del menú en pantallas grandes */
}

/* Estilos para el navbar en dispositivos móviles */
@media (max-width: 768px) {
  .Nav ul {
    display: none;
    flex-direction: column;
    align-items: center;
  }

  .Nav ul.open {
    display: flex;
  }

  .Nav ul li {
    margin-right: 0;
    margin-bottom: 10px;
  }

  .menu-icon {
    display: block; /* Muestra el icono del menú en pantallas pequeñas */
    cursor: pointer;
  }

  .menu-line {
    width: 25px;
    height: 3px;
    background-color: #fff;
    margin: 5px;
  }
}
